.mkd-sidebar-holder aside.mkd-sidebar,
.wpb_widgetised_column {

    .widget_mkd_info_widget {
        background-color: $first-main-color;
        background-image: url('./img/widget_info.png');
        background-position: 107% 126%;
        background-repeat: no-repeat;
        padding: 30px 32px 15px 32px;

        .mkd-widget-title {
            margin-bottom: 21px;
            color: #fff;
            padding-bottom: 22px;
        }

        .mkd-sidearea-title, .mkd-info-text {
            color: #fff;
        }

        .mkd-info-phone {
            font-family: $headings-font-family;
            margin-top: 21px;
            color: #fff;

            span {
                display: inline-block;
                margin-top: -5px;
                padding-right: 7px;
                vertical-align: middle;
                font-size: 18px;
            }

            a {
                font-size: 13px;
                color: #fff;
            }
        }
    }
}