/* #Reset default styles - start
   ========================================================================== */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font,
ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
	background: transparent;
	border: 0;
	margin: 0;
	padding: 0;
	vertical-align: baseline;
	outline: none;
}

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section {
	display: block;
}

blockquote {
	quotes: none;
}

blockquote:before, blockquote:after {
	content: '';
	content: none;
}

del {
	text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
	border-collapse: collapse;
	border-spacing: 0;
	border: medium none;
	vertical-align: middle;
}

table th {
	border: 0;
	padding: 5px 7px;
	text-align: center;
}

table td {
	padding: 5px 10px;
	text-align: center;
}

a img {
	border: none;
}

img, .wp-caption {
	max-width: 100%;
	height: auto;
}

iframe {
	max-width: 100%;
}

select::-ms-expand {
	display: none;
}


/* #Reset default styles - end
   ========================================================================== */

/* #General styles that are used in our theme - start
 * Here are defined global styles on body element, on container elements that hold every page,
 * headings and links, inputs, core wp styles and default pagination styles
   ========================================================================== */

.clearfix:before,
.clearfix:after {
	content: " "; /* 1 */
	display: table; /* 2 */
}

.clearfix:after {
	clear: both;
}

.clearfix {
	*zoom: 1;
}

.clear:after {
	clear: both;
	content: "";
	display: block;
}

html {
	height: 100%;
	margin: 0 !important;
	@include mkdTransition(all 0.33s ease-out);
	overflow-y: scroll !important; /* to prevent page jumping on ajax and when there is not enough content */
}

body {
	font-family: $default-font-family;
	font-size: $default-text-size;
	line-height: $default-text-line-height;
	color: $default-text-color;
	font-weight: 400;

	background-color: $default-background-color;
	overflow-x: hidden !important;
	-webkit-font-smoothing: antialiased;
}

h1 {
	font-size: 60px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.3em;
}

h2 {
	font-size: 45px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.33em;
}

h3 {
	font-size: 19px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.37em;
}

h4 {
	font-size: 15px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.3em;
}

h5 {
	font-size: 12px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.3em;
}

h6 {
	font-size: 10px;
	margin-top: 10px;
	margin-bottom: 10px;
    line-height: 1.3em;
}

h1, h2, h3, h4, h5, h6 {
	color: $headings-text-color;
	font-family: $headings-font-family;
	font-weight: 700;
    letter-spacing: 0.2em;
    text-transform: uppercase;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
	@include mkdTypographyLayout();
}

h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover {
	color: $first-main-color;
}

a,
p a {
	color: $first-main-color;
	text-decoration: none;
	cursor: pointer;

	@include mkdTransition(color 0.25s ease-in-out);
}

input[type='submit'],
.woocommerce input[type='button'],
.woocommerce-page input[type='button'] {
	-webkit-appearance: none;
}

ul, ol {
	list-style-position: inside;
}

pre {
	background-color: $default-background-color;
	padding: 15px;
	max-width: 100%;
	white-space: pre-wrap;
	word-wrap: break-word;
	margin: 15px 0;
}

dt {
	font-size: 15px;
	font-weight: 700;
	margin-bottom: 10px;
}

dd {
	margin-bottom: 15px;
}

p {
	margin: 10px 0px;
}
sub, sup {
	font-size: 75%;
	height: 0;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	top: 0.5ex;
}

sup {
	bottom: 0.5ex;
}

.wp-caption-text {
	margin: 0;
	font-style: italic;
}

table {
	width: 100%;
}

table thead tr, table tbody tr {
	border: 1px solid #e6e6e6;
}

table tbody th {
	border-right: 1px solid #e6e6e6;
}

blockquote:not(.mkd-blockquote-shortcode) {
	padding: 0;
	border: none;
	font-style: italic;
	font-size: 17px;
	line-height: 1.6em;
	margin: 20px 0;
}

blockquote cite {
	font-style: normal;
	font-weight: 500;
}

/* Webkit */
::selection {
	background: $first-main-color;
	color: $default-background-color;
}

/* Gecko/Mozilla */
::-moz-selection {
	background: $first-main-color;
	color: $default-background-color;
}

.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption,
.bypostauthor {
	opacity: 1;
}

.alignleft {
	float: left;
	margin: 0 20px 20px 0;
}

.alignright {
	float: right;
	margin: 0 0 20px 20px;
}

.aligncenter {
	display: block;
	margin: 10px auto;
}

.text-align-right {
	text-align: right;
}

.text-align-left {
	text-align: left;
}

.text-align-center {
	text-align: center;
}

.meta {
	display: none;
}

.mkd-wrapper {
	background-color: $default-background-color;
	position: relative;
	z-index: 1000;
	@include mkdTransition(left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1));
	left: 0px;

	.mkd-wrapper-inner {
		width: 100%;
		overflow: hidden;
	}
}
body.mkd-paspartu-enabled .mkd-wrapper-paspartu{
    position: relative;
    left: 0;
    background-color: $first-main-color;
    padding: 15px;
    @include mkdBoxSizing(border-box);
    @include mkdTransition(transform 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1));
    z-index: 1000;
}

.mkd-meta {
	display: none;
}

.mkd-content {
	margin-top: 0;
	position: relative;
	z-index: 100;
	background-color: $default-background-color; /*this is needed because of the uncovering footer*/

	ul ul,
	ol ol {
		padding: 0 0 0 15px;
	}

	ol ul,
	ul ol,
	ul ul,
	ol ol {
		margin: 0;
	}

	ul,
	ol {
		margin: 15px 0;
	}
}

