/*visual composer override styles*/
.wpb_row, .wpb_content_element,
ul.wpb_thumbnails-fluid > li,
.last_toggle_el_margin, .wpb_button {
	margin-bottom: 0 !important; // important is in order to prevent visual composer styles in all cases
}

.mkd-content {
	.mkd-content-inner > .mkd-container > .mkd-container-inner,
	.mkd-content-inner > .mkd-full-width > .mkd-full-width-inner {
		padding: 64px 0;
	}
}

.mkd-boxed {

	overflow: hidden !important; /* this is because content jumping while using accordion and display:inline-block on 'body.mkd-boxed .mkd-wrapper'   */

	.mkd-wrapper {
		display: inline-block;
		vertical-align: middle;
		width: 100%;

		.mkd-wrapper-inner {
			width: $grid-width-boxed;
			overflow: visible;
			margin: auto;
		}
	}

	.mkd-content {
		overflow: hidden;
	}

	.mkd-footer-inner {
		width: $grid-width-boxed;
		margin: auto;
	}

	.mkd-page-header .mkd-sticky-header,
	.mkd-page-header .mkd-fixed-wrapper.fixed {
		width: $grid-width-boxed;
		left: auto;
	}
}

.mkd-smooth-transition-loader {
    background-color: $first-main-color;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 2000;

	&.mkd-hide-spinner {

		.mkd-st-loader {
			display: none;
		}

	}

}

.mkd-container {
	background-color: #fff; //beacuse of the Mikado Slider, since content have to go over it
	position: relative;
	padding: 0;
	width: 100%;
	z-index: 100;

	&:before {
		content: " ";
		display: table;
	}

	&:after {
		content: " ";
		display: table;
		clear: both;
	}
}

.mkd-container-inner { //not in parent selector since this class is used for positioning other elements in grid
	width: $grid-width;
	margin: 0px auto;
}

.mkd-full-width {
	position: relative;
	z-index: 100;
}

